rales, como el uso de predadores como aves, gatos, perros y serpientes. Además, se pueden aplicar métodos culturales para manipular el hábitat y realizar una higienización adecuada. Los métodos mecánicos involucran el uso de barreras físicas, como guarda-ratas, dispositivos electromagnéticos, ultrasónicos y luz UV. Por último, se pueden emplear métodos químicos, como atrayentes, repelentes, esterilizantes químicos y venenos, como fumigantes y anticoagulantes.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema19/img-93.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Métodos de control de moscas")]),_c('p',[_vm._v("Se detallan los métodos utilizados para el control de moscas, tanto en sus etapas adultas como de larvas. Para las etapas adultas, se recomiendan métodos mecánicos, como el uso de luz UV y trampas adhesivas. Para las larvas, se recomienda la remoción frecuente del abono y el uso de agua hirviendo. También se pueden emplear métodos biológicos, utilizando depredadores, parasitoides y microorganismos eficientes. Los métodos químicos, como la fumigación, el pulverizado y los métodos de pintado, también son opciones para el control de moscas.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema19/img-94.png"),"alt":"Texto que describa la imagen"}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("Métodos de control de escarabajos")]),_c('p',[_vm._v("El método principal es el químico, el cual implica la aplicación de choque, la remoción de la cama y la humectación de los pisos. Además, se recomienda utilizar insecticidas de efecto residual y aplicarlos en áreas de almacenamiento de camas y alrededores. También se pueden utilizar láminas de cartón o zinc en aves de postura o reproductoras.")])]),_c